(function() {
  'use strict';
  angular.module('dbFront').controller('MyLifePathController', function($scope, $state, storage, Loading, $mdDialog, $mdMedia) {
    'ngInject';
    var vm;
    vm = this;
    vm.paths = $scope.user.user.paths;
    vm.header_text = $scope.user.user.user_name + "'s life path!";
    vm.openDialog = function(ev, path) {
      var useFullScreen;
      if (path == null) {
        path = {};
      }
      useFullScreen = ($mdMedia('sm') || $mdMedia('xs')) && $scope.customFullscreen;
      return $mdDialog.show({
        controller: 'MyLifePathFormController',
        templateUrl: 'app/pages/user/my_life_path/form.html',
        controllerAs: 'path_form',
        parent: angular.element(document.body),
        targetEvent: ev,
        locals: {
          path: path
        },
        openFrom: '#header',
        clickOutsideToClose: true,
        fullscreen: useFullScreen
      }).then(function(paths) {
        return vm.paths = paths;
      }, function() {});
    };
  });

}).call(this);



