(function() {
  'use strict';
  angular.module('dbFront').factory("SessionApi", function($location, $resource, Settings) {
    var url;
    url = Settings.api_url;
    return $resource(url + "/sessions", null, {
      login: {
        method: 'POST',
        isArray: false
      },
      check: {
        method: 'GET',
        url: "/session/check",
        params: {
          auth_token: '@auth_token'
        },
        isArray: false
      },
      logout: {
        method: 'DELETE',
        isArray: false
      }
    });
  });

}).call(this);



