(function() {
  angular.module('dbFront').config(function($stateProvider, $urlRouterProvider) {
    'ngInject';
    var header;
    header = {
      top_header: {
        templateUrl: 'app/components/header/top_header.html',
        controller: 'HeaderController',
        controllerAs: 'header'
      },
      header: {
        templateUrl: 'app/components/header/header.html',
        controller: 'HeaderController',
        controllerAs: 'header'
      }
    };
    $stateProvider.state('login', {
      url: '/',
      views: {
        header: header.top_header,
        content: {
          templateUrl: 'app/pages/login/login.html',
          controller: 'LoginController',
          controllerAs: 'login'
        }
      }
    }).state('user', {
      url: '/user/:id',
      views: {
        header: header.header,
        content: {
          templateUrl: 'app/pages/user/user.html',
          controller: 'UserController',
          controllerAs: 'user'
        }
      }
    });
    return $urlRouterProvider.otherwise('/');
  });

}).call(this);



